.healthyTeasthyEasy {
  width: 100%;
  height: 100%;
  background-image: url("../../../../img/blog.bg-img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-attachment: fixed;
  overflow: hidden;
}

.section {
  margin-bottom: 100px;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding-top: 200px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 82%;
  padding: 20px;
  background: #fff;
}

.item {
  display: flex;
  justify-content: space-between;
  min-height: 320px;
  margin-bottom: 20px;
  width: 920px;
  border: 1px solid rgb(254, 230, 196);
  background: rgba(255, 255, 255);
}

.imgMacchees,
.imgFishday {
  display: block;
  width: 420px;
  height: 320px;
  margin-right: 30px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.imgMacchees {
  background-image: url("../../../../img/mac&chees.122.webp");
}

.imgFishday {
  background-image: url("../../../../img/salmon2-1200.webp");
}

.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 50%;
  padding: 36px;
}

.topTitle {
  padding: 8px 14px;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 300;
  color: #fff;
  background: rgb(121, 69, 119);
}

.title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 28px;
  font-weight: 300;
  line-height: 30px;
  color: rgb(121, 69, 119);
}

.desc {
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
}

.title:hover,
.topTitle:hover,
.desc:hover {

  opacity: .75;
}