.about {
  width: 100%;
  overflow: hidden;
  background: rgba(148, 132, 161, 0.67);
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 40px;
  padding-top: 140px;
}

.myBlock {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  background: rgb(219, 219, 219);
}

.myPic {
  width: 55%;
  height: 850px;
  margin-right: 80px;
  padding: 60px 40px;
  background-color: rgb(148, 132, 161);
  text-align: center;
  object-fit: cover;
}

.myContent {
  width: 45%;
  padding-left: 60px;
}

.title {
  margin: 0;
  margin-bottom: 30px;
  font-size: 40px;
  font-weight: 300;
}

.text {
  margin: 0;
  width: 60%;
  font-size: 16px;
  font-weight: 300;
  line-height: 34px;
}

.text:first-of-type {
  margin-bottom: 20px;
}

.social {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 40px;
}

@media (max-width: 1200px) {
  .myContent {
    padding-left: 30px;
  }

  .text {
    width: 80%;
  }
}

@media (max-width: 980px) {
  .myPic {
    margin-right: 40px;
    padding: 0;
    background-color: transparent;
    text-align: start;
    font-size: 0;
  }
}

@media (max-width: 768px) {
  .myBlock {
    flex-direction: column;
  }

  .myPic {
    width: 100%;
    margin-right: 0;
    padding: 40px;
    background-color: rgb(148, 132, 161);
    text-align: center;
    font-size: 0;
  }

  .myContent {
    width: 74%;
    padding: 40px;
  }

  .text {
    width: 100%;
  }

  .myInfo {
    height: 100px;
  }
}

@media (max-width: 568px) {
  .container {
    padding: 12px;
    padding-top: 140px;
  }

  .myPic {
    padding: 0;
  }

  .myContent {
    width: 76%;
    padding: 40px 20px;
  }
}

@media (max-width: 320px) {
  .container {
    padding: 12px;
    padding-top: 140px;
  }

  .myContent {
    width: 100%;
    padding: 40px 14px;
  }
}
