.main {
  /* height: 100vh; */
}

.nutrition {
  width: 100%;
  background-image: url("../../img/blog.bg-img.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 60% 100%;
  background-attachment: fixed;
}

.container {
  margin: 0 auto;
  max-width: 980px;
  padding-top: 30px;
  overflow: hidden;
}

.hero {
  margin-bottom: 40px;
  padding: 30px;
  padding-top: 120px;
}

.form {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 40px 0;
  margin-bottom: 20px;
}

.input {
  display: inline-block;
  padding: 20px;
  margin-right: 10px;
  min-width: 300px;
  font-size: 22px;
  color: rgb(81, 46, 79);
  background: transparent;
  border: none;
  border-bottom: 2px solid rgb(81, 46, 79);
  outline: none;
}

.input::placeholder {
  color: rgba(81, 46, 79, 0.6);
}

.btn path {
  stroke: rgb(81, 46, 79);
}

.titleBg {
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  margin: 0;
  padding: 34px;
  text-align: center;
  font-size: 56px;
  font-weight: 400;
  color: rgb(81, 46, 79);
  background: rgba(255, 255, 255, 0.3);
}

.content {
  min-height: 1500px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;
}

.item {
  max-width: 45%;
  min-height: 460px;
  margin-bottom: 30px;
  padding: 48px;
  background: rgba(255, 255, 255, 0.8);
}

.itemBtn {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* padding-bottom: 350px; */
  text-align: start;
}

.subtitle {
  margin: 0;
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 400;
  line-height: normal;
}

.text {
  margin: 0;
  margin-bottom: 30px;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
}

.img1,
.img2,
.img3,
.img4,
.img5,
.img6 {
  display: inline-block;
  width: 320px;
  height: 212px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.img1 {
  background-image: url("../../img/diet-1.webp");
}

.img2 {
  background-image: url("../../img/diet-2.webp");
}

.img3 {
  background-image: url("../../img/diet-3.webp");
}

.img4 {
  background-image: url("../../img/diet-1.webp");
}

.img5 {
  background-image: url("../../img/diet-5.webp");
}

.img6 {
  background-image: url("../../img/diet-6.webp");
}



@media (max-width: 980px) {
  .img1,
  .img2,
  .img3,
  .img4,
  .img5,
  .img6 {
    width: 450px;
  }
}

@media (max-width: 768px) {
  .hero {
    padding: 16px;
    padding-top: 100px;
  }

  .title {
    padding: 24px 34px;
  }

  .item {
    min-width: 90%;
  }
}

@media (max-width: 568px) {
  .hero {
    margin-bottom: 10px;
    padding: 0;
    padding-top: 60px;
  }

  .container {
    padding-top: 10px;
  }

  .input {
    min-width: 240px;
  }

  .titleBg {
    min-height: 100px;
  }

  .title {
    font-size: 34px;
    padding: 12px 24px;
    background: none;
  }

  .list {
    padding: 0;
  }

  .item {
    min-width: 100%;
    margin-bottom: 5px;
  }

  .img1,
  .img2,
  .img3,
  .img4,
  .img5,
  .img6 {
    width: 200px;
    background-size: auto;
  }
}
