.container {
  margin: 0 auto;
  max-width: 1200px;
}

.about {
  background: #fff;
}

.block {
  display: flex;
  margin: 0 auto;
  min-height: 655px;
  flex-wrap: wrap;
  max-width: 980px;
  padding: 50px 20px;
  background-color: rgba(203, 188, 203, 0.32);
}

.img {
  display: inline-block;
  margin-right: 40px;
  width: 544px;
  height: 544px;
  object-fit: cover;
  border-radius: 5px;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
}

.desc {
  max-width: 328px;
  padding-left: 30px;
}

.title {
  margin: 0;
  margin-left: -15px;
  margin-bottom: 20px;
  padding: 20px 0;
  font-size: 30px;
  font-weight: 400;
  color: rgb(81, 46, 79);
}

.text {
  margin: 0;
  font-size: 16px;
  font-weight: 300;
  line-height: 34px;
}

.text:last-of-type {
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  padding: 16px 56px;
  border: 1px solid rgb(81, 46, 79);
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  transition: 0.3s color ease-in-out, 0.3s background ease-in-out;
}

.btn:hover {
  color: #fff;
  background: rgb(81, 46, 79);
}


@media (max-width: 980px) {

  .block {
    min-height: 320px;
    padding: 30px 20px;
  }

  .about-img {
    width: 380px;
    height: 400px;
    margin-right: 4px;
  }

  .title {
    margin-bottom: 10px;
  }

  .text {
    line-height: 24px;
  }
}

@media (max-width: 768px) {
  .block {
    flex-direction: column-reverse;
    align-items: center;
  }

  .desc {
    max-width: 70%;
    margin-bottom: 30px;
  }

  .about-img {
    width: 500px;
    height: 500px;
  }
}

@media (max-width: 568px) {
  .block {
    width: 100%;
  }

  .desc {
    padding: 0;
    max-width: 100%;
  }

  .title {
    margin-left: 0;
  }

  .about-img {
    width: 300px;
    height: 300px;
  }
}