.footer {
  width: 100%;
  min-height: 100px;
  background: rgb(203, 188, 203);
  box-shadow: rgba(0, 0, 0, 0.7) 0px 0px 5px 0px;
}

.info {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-end;
  padding-top: 20px;
}

.social {
  padding: 0 40px;
}

.socilaItem {
  display: block;
  padding: 4px 6px;
  border-radius: 5px;
  font-size: 18px;
}

/* .footer-info .aboutme-nav-item {
  color: rgb(81, 46, 79);
}

.footer-info .aboutme-nav {
  padding-top: 0;
} */

.footerList {
  padding-top: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 40px;
}

.footerItem {
  padding: 2px 3px;
  border-radius: 5px;
  font-size: 18px;
  color: rgb(81, 46, 79);
}

.footerItem:not(:last-child):not(:last-child) {
  margin-right: 20px;
}

.footerItem:hover {
  color: #fff;
}

.socialItem {
  display: block;
  padding: 4px 6px;
  border-radius: 5px;
  font-size: 30px;
}

.socialItem svg g {
  fill: rgb(81, 46, 79);
}

.socialItem svg path,
.socialItem svg circle {
  stroke: rgb(81, 46, 79);
}

.socialItem:hover svg path,
.socialItem:hover svg circle {
  stroke: #fff;
}

.socialItem:hover svg g {
  fill: #fff;
}

@media (max-width: 768px) {
  .footer {
    min-height: 56px;
  }
}
