.main {
  width: 100%;
  height: 100%;
  background-image: url("../../../img/avocado.bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.section {
  margin: 0 auto;
  padding: 200px 40px 40px 40px;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 30px 60px;
  max-width: 980px;
  background: rgba(255, 255, 255, 0.2);
}

.components {
  margin: 0 auto;
  width: 90%;
  padding: 20px 60px;
  background: rgba(255, 255, 255, 0.6);
}

.title {
  margin: 0;
  margin-bottom: 40px;
  padding-top: 40px;
  font-size: 50px;
  font-weight: 400;
  line-height: normal;
}

.subtitle,
.lessTitle {
  margin: 0;
  margin-bottom: 30px;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  font-style: italic;
  color: rgb(121, 69, 119);
}

.cookingTime {
  display: block;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  font-style: italic;
}

.cookingTime:not(:last-child) {
  margin-bottom: 10px;
}

.imgMacchees {
  display: inline-block;
  width: 600px;
  height: 600px;
  margin-bottom: 20px;
  background-image: url('../../../img/mac&chees.122.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.list {
  margin-bottom: 30px;
}

.item {
  margin: 0;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.85);
  font-size: 18px;
  font-weight: 200;
  line-height: 24px;
}

.text {
  display: block;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 100;
  line-height: 24px;
  font-style: italic;
  color: rgb(121, 69, 119);
}

@media (max-width: 980px) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }
}

@media (max-width: 768px) {
  .container {
    background: none;
  }

  .components {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    background: rgba(255, 255, 255, 0.8);
  }
}

@media (max-width: 320px) {
  .components {
    padding-left: 10px;
    padding-right: 10px;
  }

  .title {
    font-size: 36px;
  }
}
