
.blog {
  width: 100%;
  background-image: url("../../img/blog.bg-img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-attachment: fixed;
  overflow: hidden;
}

.section {
    margin-bottom: 100px;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding-top: 200px;
}

.content {
  min-height: 975px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 40px;
  background: rgba(255, 255, 255, 0.3);
}

.item {
  width: 32%;
  min-height: 420px;
  margin-bottom: 20px;
  border: 1px solid rgb(203, 188, 203);
  background: rgba(255, 255, 255, 0.5);
}

.btn {
  display: block;
  min-height: 370px;
  margin-bottom: 50px;
}

.desc {
  padding: 0 15px;
}

.date {
  display: inline-block;
  margin-bottom: 20px;
  font-size: 12px;
  font-weight: 400;
  line-height: normal;
}

.title {
  margin: 0;
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: 400;
  line-height: 30px;
}

.text {
  margin: 0;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
}

.img1,
.img2,
.img3,
.img4,
.img5,
.img6 {
  display: inline-block;
  margin-bottom: 20px;
  width: 100%;
  height: 240px;
  border-bottom: 1px solid rgb(239, 239, 239);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.img1 {
  background-image: url("../../img/gallery-1-img.webp");
}

.img2 {
  background-image: url("../../../src/img/gallery-2-img.webp");
}

.img3 {
  background-image: url("../../img/gallery-3-img.webp");
}

.img4 {
  background-image: url("../../img/blog-img-4.webp");
}

.img5 {
  background-image: url("../../img/blog-img-5.webp");
}

.img6 {
  background-image: url("../../img/blog-img-6.webp");
}

.view,
.comment {
  display: inline-block;
  height: 30px;
  width: 30px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.view {
  background-image: url("../../img/view.svg");
}

.comment {
  background-image: url("../../img/comment.svg");
}

@media (max-width: 980px) {
  .item {
    min-width: 49%;
  }
}

@media (max-width: 768px) {
  .recipes-list {
    display: none;
  }

  .blog-page-list {
    justify-content: center;
  }

  .item {
    min-width: 90%;
    min-height: 680px;
    margin-bottom: 30px;
  }

  .img1,
  .img2,
  .img3,
  .img4,
  .img5,
  .img6 {
    height: 400px;
    margin-bottom: 30px;
  }

  .icons {
    display: flex;
    justify-content: flex-end;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .icons::before {
    top: 240%;
  }

  .icons button {
    position: relative;
    top: 165px;
    right: 40px;
  }
}

.icons {
  display: flex;
  justify-content: flex-end;
}

.icons {
  position: relative;
  padding: 12px 16px;
}

.icons::before {
  content: "";
  position: absolute;
  top: -20%;
  left: 10px;
  height: 1px;
  width: 90%;
  background: rgba(0, 0, 0, 0.2);
}
@media (max-width: 568px) {
  .blog-page-list {
    padding: 0;
  }

  .item {
    min-width: 100%;
    min-height: 680px;
  }

  .img1,
  .img2,
  .img3,
  .img4,
  .img5,
  .img6 {
    height: 360px;
  }
}
