.category {
  width: 100%;
  background-image: url("../../img/blog.bg-img.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  overflow: hidden;
}

.container {
  max-width: 1200px;
  height: 100vh;
  margin: 0 auto;
  padding: 200px 0;
}

.title {
  margin: 0;
  margin-bottom: 40px;
  text-align: center;
  font-size: 60px;
  color: rgb(81, 46, 79);
}

.nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 930px;
}

.list {
  /* background: rgb(203, 188, 203); */
  width: 458px;
  padding: 16px 10px;
  border-top: none;
}

.item {
  min-height: 60px;
  padding: 20px 10px;
  margin-bottom: 20px;
  color: rgb(81, 46, 79);
  background: rgb(156 123 156 / 54%);
  transition: 0.3s background ease-in-out;
  border-bottom: 1px solid rgb(81, 46, 79);
}

.item:hover {
  background: rgba(203, 188, 203, 0.9);
  box-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.67);
}

@media (max-width: 768px) {
  .title {
    font-size: 40px;
  }

  .item {
    font-size: 24px;
  }
}

@media (max-width: 320px) {
  .title {
    font-size: 30px;
  }

  .list {
    width: 100%;
  }

  .item {
    font-size: 20px;
  }
}
