.main {
  width: 100%;
  height: 100%;
  background-image: url("../../../img/avocado.bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.section {
  margin: 0 auto;
  padding: 200px 40px 40px 40px;
}

