  position: fixed;
  top: 240px;
  left: 0;
  width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: start;
} */

.container {
  margin: 0 auto;
  padding: 30px 60px;
  max-width: 980px;
  background: rgba(255, 255, 255, 0.2);
}

.components,
.idea,
.calories {
  margin: 0 auto;
  width: 90%;
  padding: 20px 60px;
  background: rgba(255, 255, 255, 0.6);
}

.title {
  margin: 0;
  margin-bottom: 40px;
  padding-top: 40px;
  font-size: 50px;
  font-weight: 400;
  line-height: normal;
}

.subtitle {
  margin: 0;
  margin-bottom: 30px;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  font-style: italic;
  color: rgb(121, 69, 119);
}

.avocadoInfo {
  margin: 0;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  color: rgb(121, 69, 119);
  font-style: none;
}

.img {
  width: 600px;
  height: 700px;
  object-fit: cover;
  margin-bottom: 20px;
}

.desc {
    margin: 0;
    margin-bottom: 14px;
    color: rgba(0, 0, 0, 0.85);
    font-size: 20px;
    font-weight: 300;
    line-height: 24px;
}

.clickme {
  /* display: inline-block; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.clickme a {
  padding: 18px 12px;
  border-radius: 30px;
  font-size: 12px;
  font-weight: 100;
  color: rgb(98, 26, 62);
  background-color: rgb(241, 222, 222);
}

.idea {
  position: relative;
}

.ideaTitle,
.ideaText,
.info {
  margin: 0;
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: 400;
  line-height: 30px;
  font-style: italic;
  color: rgb(121, 69, 119);
}

.info {
  font-size: 20px;
  font-weight: 600;
}

.ideaText {
  margin-bottom: 0;
  font-size: 18px;
}

.ideaText::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 230px;
  background: rgb(121, 69, 119);
  left: 20px;
  top: 26%;
}

.btnUp {
  display: inline-block;
  position: sticky;
  top: 97%;
  left: 78%;
  transform: rotate(90deg);
  height: 34px;
  width: 38px;
  border-radius: 5px;
  box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.5);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../../../img/back-svgrepo-com.svg");
}

@media (max-width: 1200px) {
  .avocado {
    top: 100px;
  }

  .btnUp {
    left: 80%;
  }
}

@media (max-width: 980px) {
  .avocado {
    position: absolute;
    top: -100px;
  }

  .container {
    padding-left: 0;
    padding-right: 0;
  }
  .btnUp {
    left: 85%;
  }
}

@media (max-width: 768px) {
  .container {
    background: none;
  }

  .components,
  .idea,
  .calories {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    background: rgba(255, 255, 255, 0.8);
  }

  .img {
    height: 460px;
  }


  .btnUp {
    position: fixed;
    top: 93%;
    left: 90%;
    z-index: 10;
  }

  .ideaText::before {
    width: 1px;
    height: 160px;
    left: 5px;
    top: 26%;
  }
}

@media (max-width: 568px) {
  .avocado {
    padding-left: 0;
    padding-right: 0;
  }
}


@media (max-width: 320px) {

  .title {
    font-size: 36px;
  }

  .components,
  .idea,
  .calories {
    padding-left: 10px;
    padding-right: 10px;
  }

  .img {
    height: 320px;
  }

}