.blog {
  background: #fff;
}

.container {
  margin: 0 auto;
  /* max-width: 1200px; */
  max-width: 1794px;
}

.block {
  display: flex;
  max-width: 1794px;
  min-height: 480px;
  background: rgb(228, 220, 228);
}

.img {
  width: 50%;
  margin-right: 100px;
  background-image: url("../../../img/hero-2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  padding: 20px;
}

.title {
  margin: 0;
  margin-bottom: 30px;
  font-size: 30px;
  font-weight: 700;
}

.text {
  width: 440px;
  margin: 0;
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
}

.btn {
  max-width: 240px;
  text-align: center;
  display: inline-block;
  padding: 16px 40px;
  border: 1px solid rgb(81, 46, 79);
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  transition: 0.3s color ease-in-out, 0.3s background ease-in-out;
}

.btn:hover {
  color: #fff;
  background: rgb(81, 46, 79);
}

@media (max-width: 980px) {

  .blog-block {
    min-height: 350px;
  }

  .blog-img {
    height: 350px;
    margin-right: 50px;
  }

  .blog__text {
    width: 340px;
  }
}

@media (max-width: 768px) {
  .blog-block {
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
  }

  .blog-desc {
    width: 68%;
  }

  .blog-img {
    margin-right: 0;
    width: 65%;
    height: 340px;
  }
}

@media (max-width: 568px) {
  .blog-img {
    width: 90%;
    border-radius: 5px;
  }

  .blog-desc {
    width: 90%;
  }

  .blog__text {
    width: 100%;
  }
}
