.header {
  /* position: absolute;
  top: 0;
  left: 0; */
  width: 100%;
  min-height: 100px;
  background: rgb(203, 188, 203);
  box-shadow: rgba(0, 0, 0, 0.7) 0px 0px 5px 0px;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero {
  position: relative;
}

.list {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 700;
  /* line-height: 56px; */
  color: rgb(81, 46, 79);
}

.navLink {
  padding: 10px;
  outline: none;
}

.navLink:hover {
  color: #fff;
  background: rgba(81, 46, 79);
}



.item {
  padding: 12px 20px;
  outline: none;
}

.item:last-child {
  margin-right: 20px;
}

.item:hover {
  color: #fff;
  background: rgba(81, 46, 79);
}

.socialItem {
  width: 10%;
  display: inline-block;
  padding: 4px 2px;
  border-radius: 5px;
  font-size: 26px;
  line-height: 26px;
}

.socialItem svg g {
  fill: rgb(81, 46, 79);
}

.socialItem svg path,
.socialItem svg circle {
  stroke: rgb(81, 46, 79);
}

.socialItem:hover {
  opacity: 0.5;
}

