.main {
  width: 100%;
  height: 100%;
  background-image: url("../../../img/avocado.bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.section {
  margin: 0 auto;
  padding: 200px 40px 40px 40px;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 30px 60px;
  max-width: 980px;
  background: rgba(255, 255, 255, 0.2);
}

.btnUp {
  display: inline-block;
  position: sticky;
  top: 97%;
  left: 78%;
  transform: rotate(90deg);
  height: 34px;
  width: 38px;
  border-radius: 5px;
  box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.5);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../../../img/back-svgrepo-com.svg");
}

.cooking {
  position: fixed;
  top: 240px;
  left: 0;
  width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.recipes {
  width: 200px;
  font-size: 16px;
  font-weight: 300;
  padding: 5px 15px;
  margin-right: 10px;
  color: rgb(0, 42, 3);
  background: rgba(255, 255, 255, 0.6);
}

.recipes {
  margin-bottom: 3px;
}

.recipes:hover {
  color: #fff;
  background: rgba(26, 83, 0, 0.6);
}

.recipes a {
  display: block;
}

.components {
  margin: 0 auto;
  width: 90%;
  padding: 20px 60px;
  background: rgba(255, 255, 255, 0.6);
}

.btnBack {
  position: absolute;
  top: 2%;
  left: 16%;
  height: 34px;
  width: 38px;
  border-radius: 5px;
  box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.5);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../../../img/back-svgrepo-com.svg");
}

.title {
  margin: 0;
  margin-bottom: 40px;
  padding-top: 40px;
  font-size: 50px;
  font-weight: 400;
  line-height: normal;
}

.subtitle,
.lessTitle {
  margin: 0;
  margin-bottom: 30px;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  font-style: italic;
  color: rgb(121, 69, 119);
}

.lessTitle {
  font-size: 20px;
}

.text {
  margin: 0;
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  font-style: italic;
}

.cookingTime {
  display: block;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  font-style: italic;
}

.cookingTime:not(:last-child) {
  margin-bottom: 10px;
}

.imgShuros,
.imgComponents {
  display: inline-block;
  width: 600px;
  height: 600px;
  margin-bottom: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.imgShuros {
  background-image: url("../../../img/shorush_1200.jpg");

}

.imgComponents {
  background-image: url("../../../img/shorus-comp.webp");
}

.list {
  margin-bottom: 30px;
}

.item {
  margin: 0;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.85);
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.testyTitle {
    margin: 0;
    margin-bottom: 30px;
    padding-top: 30px;
    font-size: 26px;
    font-weight: 500;
    line-height: 30px;
}

.videoBlock {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.video {
  width: 700px;
  height: 680px;
}

.hash {
  font-size: 20px;
  line-height: 56px;
  padding: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.hashText {
  font-size: 20px;
  font-style: italic;
  color: rgb(121, 69, 119)
}

@media (max-width: 1200px) {
  .cooking {
    top: 100px;
  }

  .btnUp {
    left: 80%;
  }
}

@media (max-width: 980px) {
  .cooking {
    position: absolute;
    top: -100px;
  }

  .container {
    padding-left: 0;
    padding-right: 0;
  }

  .btnUp {
    left: 85%;
  }
}

@media (max-width: 768px) {
  .container {
    background: none;
  }

  .btnUp {
    position: fixed;
    top: 93%;
    left: 90%;
    z-index: 10;
  }

  .components {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    background: rgba(255, 255, 255, 0.8);
  }

  .imgShuros {
    height: 460px;
  }
}

@media (max-width: 568px) {
  .section {
    padding-left: 0;
    padding-right: 0;
  }

  .text {
    width: 90%;
  }
}

@media (max-width: 320px) {
  .components {
    padding-left: 10px;
    padding-right: 10px;
  }

  .title {
    font-size: 36px;
  }

  .subtitle {
    font-size: 18px;
  }

  .text {
    width: 90%;
    font-size: 16px;
  }

  .imgShuros {
    height: 320px;
  }
}
