.recipes {
  padding: 20px;
}

.container {
  width: 1200px;
  margin: 0 auto;
 
}

.list {
  display: flex;
  justify-content: center;
}

.items {
  font-size: 18px;
  color: rgb(81, 46, 79);
  cursor: pointer;
}

.items:hover {
  color: #fff;
  opacity: 0.6;
}

.items:not(:last-child) {
  margin-right: 16px;
}

@media (max-width: 768px) {
  .recipes-list {
    display: none;
  }
}