.main {
  width: 100%;
  height: 100%;
  background-image: url("../../../img/avocado.bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.section {
  margin: 0 auto;
  padding: 200px 40px 40px 40px;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 30px 60px;
  max-width: 980px;
  background: rgba(255, 255, 255, 0.2);
}

.btnUp {
  display: inline-block;
  position: sticky;
  top: 97%;
  left: 78%;
  transform: rotate(90deg);
  height: 34px;
  width: 38px;
  border-radius: 5px;
  box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.5);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../../../img/back-svgrepo-com.svg");
}

.cooking {
  position: fixed;
  top: 240px;
  left: 0;
  width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.recipes {
  width: 200px;
  font-size: 16px;
  font-weight: 300;
  padding: 5px 15px;
  margin-right: 10px;
  color: rgb(0, 42, 3);
  background: rgba(255, 255, 255, 0.6);
}

.recipes {
  margin-bottom: 3px;
}

.recipes:hover {
  color: #fff;
  background: rgba(26, 83, 0, 0.6);
}

.recipes a {
  display: block;
}

.components,
.idea {
  margin: 0 auto;
  width: 90%;
  padding: 20px 60px;
  background: rgba(255, 255, 255, 0.6);
}

.title {
  margin: 0;
  margin-bottom: 40px;
  padding-top: 40px;
  font-size: 50px;
  font-weight: 400;
  line-height: normal;
}

.subtitle,
.lessTitle {
  margin: 0;
  margin-bottom: 30px;
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  font-style: italic;
  color: rgb(121, 69, 119);
}

.lessTitle {
  font-size: 20px;
  font-weight: 200;
}

.imgShpinat {
  display: inline-block;
  width: 600px;
  height: 600px;
  margin-bottom: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url("../../../img/shpinat-1200.webp");
}

.cookingTime,
.text {
  display: block;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
}

.text {
  font-size: 18px;
  font-weight: 100;
  margin-bottom: 16px;
}

.cookingTime:not(:last-child) {
  margin-bottom: 10px;
}

.idea {
  position: relative;
}

.ideaText {
  margin: 0;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  font-style: italic;
  color: rgb(96, 46, 94);
}

.ideaText::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 50px;
  background: rgb(121, 69, 119);
  left: 5%;
  top: 50%;
}

.list {
  margin-bottom: 20px;
}

.item {
  margin: 0;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.85);
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}

.imgCooking,
.imgReady {
  display: inline-block;
  width: 600px;
  height: 600px;
  margin-bottom: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.imgCooking {
  background-image: url('../../../img/shpinat2.webp');
}

.imgReady {
  background-image: url("../../../img/shpinat3-1200.webp");
}


@media (max-width: 980px) {
  .cooking {
    position: absolute;
    top: -100px;
  }

  .container {
    padding-left: 0;
    padding-right: 0;
  }
}

@media (max-width: 768px) {
  .container {
    background: none;
  }
}
@media (max-width: 320px) {
  .components,
  .idea {
    padding-left: 10px;
    padding-right: 10px;
  }

  .title {
    font-size: 36px;
  }

  .subtitle {
    font-size: 18px;
  }

  .text {
    font-size: 16px;
  }
}
