.news {
  background: #fff;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 980px;
  min-height: 360px;
  padding: 30px 0;
  list-style: none;
}

.item {
  width: 32%;
  min-height: 320px;
  background: #fff;
  border: 1px solid rgb(203, 188, 203);
}


.text {
  width: 70%;
  height: 70px;
  padding: 0 16px;
  margin: 0;
  font-size: 19px;
  font-weight: 400;
  line-height: 30px;
}

.img1,
.img2,
.img3 {
  display: inline-block;
  margin-bottom: 20px;
  width: 100%;
  height: 240px;
  border-bottom: 1px solid rgb(239, 239, 239);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.img1 {
  background-image: url("../../../img/gallery-1-img.webp");
}

.img2 {
  background-image: url("../../../img/gallery-2-img.webp");
}

.img3 {
  background-image: url("../../../img/gallery-3-img.webp");
}



@media (max-width: 980px) {
  .container {
    padding: 0 20px;
  }

  .item {
    width: 45%;
  }
}

@media (max-width: 768px) {
  .list {
    flex-direction: column;
    align-items: center;
  }

  .item {
    width: 100%;
    margin-bottom: 30px;
  }

  .img1 {
    height: 450px;
    /* background-size: contain; */
  }
}

@media (max-width: 568px) {
  .img1,
  .img2,
  .img3 {
    width: 100%;
    height: 300px;
  }
}


@media (max-width: 320px) {
  .container {
    padding: 0;
  }

  .img1, 
  .img2,
  .img3 {
    width: 300px;
    overflow: hidden;
  }
}
