.contact {
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-attachment: fixed;
  overflow: hidden;
}

.section {
  margin-bottom: 100px;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding-top: 200px;
}

.form {
  display: flex;
  flex-direction: column;
}

input {
  border: none;
}
