.window {
  position: absolute;
  top: 100px;
  left: 360px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 464px;
  min-height: 395px;
  padding-top: 30px;
  background: rgba(255, 255, 255, 0.8);
}

.windowTitle {
  margin: 0;
  font-size: 24px;
  font-weight: 400;
  line-height: 52px;
}

.windowTitle:last-of-type {
  margin-bottom: 40px;
}

.windowBtn {
  display: inline-block;
  padding: 16px 40px;
  border: 1px solid rgb(81, 46, 79);
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  transition: 0.3s color ease-in-out, 0.3s background ease-in-out;
}

.windowBtn:hover {
  color: #fff;
  background: rgb(81, 46, 79);
}


@media (max-width: 980px) {
  .window {
    display: none;
  }
}
